"use client";

import React, { useState, useEffect } from "react";
import { Search, Mail, X,  Send } from 'lucide-react';

interface NegotiationItem {
  _id: string;
  productName: string;
  pid: string;
  hsnCode: string;
  amount: number;
  minQty: string;
  commission: number;
  measurement: string;
  gstPercentage: number;
  seller_email: string;
  buyer_email: string;
  reqId: string;
  company: string;
  pincode: string;
  mobile: string;
  created_at: string;
  description: string;
  status: string;
  negotiation: {
    negId: string;
    yourAmount: string;
    yourQty: string;
    previewAmount: string;
    previewQty: string;
    deliveryRelatedInfo: string;
    messages: string;
    createdAt: string;
  }[];
}

const getCookie = (name: string) => {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop()?.split(";").shift() || "";
  return "";
};

const Input = ({ value, onChange, placeholder, className = "" }: any) => (
  <div className="relative">
    <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-5 w-5 text-gray-400" />
    <input
      type="text"
      value={value}
      onChange={onChange}
      placeholder={placeholder}
      className={`pl-10 pr-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${className}`}
    />
  </div>
);

const Button = ({ onClick, children, className = "" }: any) => (
  <button
    onClick={onClick}
    className={`px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors ${className}`}
  >
    {children}
  </button>
);

const InfoRow = ({ icon, label, value }: { icon: React.ReactNode, label: string, value: React.ReactNode }) => (
  <div className="flex items-center mb-2">
    {icon}
    <span className="font-semibold mr-2 ml-2">{label}:</span>
    <span>{value}</span>
  </div>
);

const NegotiationCard = ({
  data,
  onClose,
  onSendNegotiation,
  onUpdateStatus,
  onSendToCustomer
}: {
  data: NegotiationItem;
  onClose: () => void;
  onSendNegotiation: (negotiationData: any) => void;
  onUpdateStatus: (id: string, newStatus: string) => void;
  onSendToCustomer: (id: string, amount: string) => void;
}) => {
  const [newAmount, setNewAmount] = useState('');
  const [newStatus, setNewStatus] = useState(data.status);
  const negotiation = data.negotiation[0];

  const handleSendNegotiation = () => {
    const negotiationData = {
      productDetails: {
        productName: data.productName,
        productId: data.pid,
        hsnCode: data.hsnCode,
        measurement: data.measurement,
        gst: `${data.gstPercentage}%`
      },
      sellerInfo: {
        email: data.seller_email
      },
      requestInfo: {
        requestId: data.reqId,
        createdAt: data.created_at
      },
      negotiationDetails: {
        negotiationId: negotiation.negId,
        negotiationAmount: newAmount || negotiation.yourAmount,
        negotiationQuantity: negotiation.yourQty,
        previewAmount: negotiation.previewAmount,
        previewQuantity: negotiation.previewQty
      },
      additionalInfo: {
        deliveryInfo: negotiation.deliveryRelatedInfo,
        description: data.description
      },
      status: newStatus
    };

    onSendNegotiation(negotiationData);
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-lg p-6 max-w-2xl w-full max-h-[90vh] overflow-y-auto thin relative">
        <button onClick={onClose} className="absolute top-4 right-4 text-gray-500 hover:text-gray-700">
          <X className="w-6 h-6" />
        </button>
        <h2 className="text-2xl font-bold mb-4">{data.productName}</h2>
        {/* ... existing InfoRow rendering as before ... */}

        <div className="mt-6 flex justify-between items-center">
          <Button onClick={handleSendNegotiation}><Send className="w-5 h-5 mr-2" /> Send Negotiation</Button>
          <Button onClick={() => onSendToCustomer(data._id, newAmount || negotiation.yourAmount)}>
            <Mail className="w-5 h-5 mr-2" /> Send to Customer
          </Button>
        </div>
      </div>
    </div>
  );
};

const Negotiation = () => {
  const [data, setData] = useState<NegotiationItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedQuotation, setSelectedQuotation] = useState<NegotiationItem | null>(null);
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    const fetchNegotiations = async () => {
      try {
        const res = await fetch("http://localhost:3033/api/v1/negotiationAll", {
          headers: {
            Authorization: `Bearer ${getCookie("token")}`
          }
        });
        if (!res.ok) throw new Error("Failed to fetch negotiations");
        const json = await res.json();
        setData(json);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchNegotiations();
  }, []);

  const handleSendNegotiation = async (negotiationData: any) => {
    try {
      const res = await fetch("http://localhost:3033/api/v1/negotaiton", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${getCookie("token")}`
        },
        body: JSON.stringify(negotiationData)
      });
      if (!res.ok) throw new Error("Failed to send negotiation");
      setSelectedQuotation(null);
    } catch (err) {
      console.error(err);
    }
  };

  const handleStatusUpdate = async (id: string, status: string) => {
    console.log(`Update status for ${id} to ${status}`);
  };

  const handleSendToCustomer = async (id: string, amount: string) => {
    console.log(`Send to customer ${id} with amount ${amount}`);
  };

  const filteredData = data.filter(item =>
    item.productName.toLowerCase().includes(searchQuery.toLowerCase())
  );

  if (loading) return <div>Loading...</div>;
  if (error) return <div>Error: {error}</div>;

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Admin Customer Quotations</h1>
      <Input
        value={searchQuery}
        onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearchQuery(e.target.value)}
        placeholder="Search by Product Name"
        className="mb-4"
      />
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {filteredData.map((quotation) => (
          <div
            key={quotation._id}
            onClick={() => setSelectedQuotation(quotation)}
            className="p-4 border border-gray-200 rounded-md cursor-pointer hover:shadow-lg transition-shadow"
          >
            <h2 className="text-xl font-semibold mb-2">{quotation.productName}</h2>
            <p><span className="font-semibold">Quotation ID:</span> {quotation._id}</p>
            <p><span className="font-semibold">Company:</span> {quotation.company}</p>
            <p><span className="font-semibold">Status:</span> {quotation.status}</p>
          </div>
        ))}
      </div>

      {selectedQuotation && (
        <NegotiationCard
          data={selectedQuotation}
          onClose={() => setSelectedQuotation(null)}
          onSendNegotiation={handleSendNegotiation}
          onUpdateStatus={handleStatusUpdate}
          onSendToCustomer={handleSendToCustomer}
        />
      )}
    </div>
  );
};

export default Negotiation;
